:root {
  --grayLight: #555;
  --greenLight: #96EEC5;
  --greenDark: #07C5BC;
}

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
.menuBar {
  text-align: right;
  padding: 4px 20px;
}

.author {
  color: var(--grayLight);
  text-align: center;
  font-size: 14px;
}
.header h1 {
    background-image: linear-gradient(to bottom right, var(--greenLight) , var(--greenDark));
    padding: 40px;
    text-align: center;
    color: white;
    font-size: 48px;
    margin: 0 0 40px 0;
}

.header p {
    text-align: center;
    font-size: 18px;
    color: var(--greenDark);
}  
.add {
    background-image: linear-gradient(to bottom right, var(--greenLight) , var(--greenDark));
    width: 20px;
    height: 20px;
    padding: 8px;
    border-radius: 4px;
    display: inline-block;
    vertical-align: top;
    margin-right: 20px;
    transition: 0.2s;
}

.add:hover {
    -webkit-transform: scale(1.05);
            transform: scale(1.05);
    cursor: pointer;
    box-shadow: rgba(0, 0, 0, 0.1) 2px 2px 4px;
}

.add:active {
    -webkit-transform: scale(1);
            transform: scale(1);
    cursor: pointer;
}
.search {
    text-align: right;
    display: inline-block;
    vertical-align: top;
}

.search input {
    border: 1px solid var(--greenDark);
    border-radius: 4px;
    padding: 8px;
    font-size: 14px;
    width: 200px;
    outline: none;
    height: 36px;
}
.add_popup-overlay {
    position: fixed;
    width: 100vw;
    height: 100vh;
    top: 0;
    left: 0;
    background-color: rgba(0,0,0,0.5);
    z-index: 1;
}

.add_popup-popup {
    position: absolute;
    width: 460px;
    height: 300px;
    box-shadow: 0 4px 8px rgba(0,0,0,0.15), 0 4px 8px rgba(0,0,0,0.20);
    background-color: white;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    border-radius: 4px;
    padding: 20px;
    z-index: 2;
}

.add_popup-close {
    width: 20px;
    height: 20px;
    background-image: linear-gradient(to bottom right, var(--greenLight) , var(--greenDark));
    border-radius: 4px;
    padding: 6px;
    float: right;
}

.add_popup-close img {
    -webkit-transform: rotate(45deg);
            transform: rotate(45deg);
}

.add_popup h3 {
    margin: 0 0 50px 0;
    color: rgb(7, 197, 188);
    font-size: 22px;
}

input.add_name, input.add_email {
    border: 1px solid var(--greenDark);
    border-radius: 4px;
    padding: 8px;
    font-size: 14px;
    width: calc(100% - 20px);
    outline: none;
    display: block;
    margin-bottom: 30px;
}

.add_popup a {
    color: var(--greenDark);
    text-decoration: underline;
    cursor: pointer;
}

.add_button {
    width: auto;
    height: 20px;
    background-image: linear-gradient(to bottom right, var(--greenLight) , var(--greenDark));
    color: white;
    font-weight: bold;
    border-radius: 4px;
    padding: 10px 24px;
    bottom: 20px;
    position: absolute;
    right: 20px;
}

.add_popup-close:hover, .add_button:hover {
    -webkit-transform: scale(1.05);
            transform: scale(1.05);
    cursor: pointer;
    box-shadow: rgba(0, 0, 0, 0.1) 2px 2px 4px;
}

.add_popup-close:active, .add_button:active {
    -webkit-transform: scale(1);
            transform: scale(1);
    cursor: pointer;
}
.scrollableContent {
    height: calc(100vh - 320px);
    overflow: scroll;
    margin-top: 16px;
}
.card {
    width: 260px;
    height: 180px;
    box-shadow: 0 4px 8px rgba(0,0,0,0.15), 0 4px 8px rgba(0,0,0,0.20);
    border-radius: 4px;
    margin: 4px 20px 36px 20px;
    flex-grow: 1;
    flex-shrink: 0;
    flex-basis: calc(33% - 40px);
    min-width: 420px;
}

.card_top {
    padding: 20px;
    text-align: center;
    width: 140px;
    display: inline-block;
}

.card img {
    width: 140px;
    height: 140px;
    border-radius: 50%;
}

.card_bottom {
    padding: 20px;
    border-left: 1px solid var(--greenDark);
    color: var(--greenDark);
    width: calc(100% - 221px);
    display: inline-block;
    vertical-align: top;
    height: 140px;
}

.card_bottom h2 {
    margin: 0 0 20px 0;
}

.card_bottom p {
    margin: 0;
    word-break: break-all;
}

@media screen and (max-width: 500px) {
    .card {
        height: 120px;
    }

    .card_top {
        width: 80px;
    }
    
    .card img {
        width: 80px;
        height: 80px;
    }

    .card_bottom {
        width: calc(100% - 161px);
        height: 80px;
    }
}

@media screen and (max-width: 460px) {
    .card {
        flex-grow: 1;
        flex-shrink: 0;
        flex-basis: calc(100% - 40px);
        min-width: 320px;
    }
}
.cardsContainer {
    display: flex;
    flex-wrap: wrap;
}
